:root {
  --background-color: #f5f3ea;
  --text-color: #252525;
  --border-color: #dedad1;
  --button-text-color: #f5f3ea;
  --subtle-text-color: #757575;
}

.dark-theme {
  --background-color: #918f8f;
  --text-color: #f5f3ea;
  --border-color: #484747;
  --button-text-color: #1a1a1a;
  --subtle-text-color: #a0a0a0;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: Gilroy-Regular, sans-serif;
  font-size: 16px;
  line-height: 1.5;
  background-color: var(--background-color);
  color: var(--text-color);
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  max-width: 1240px;
  margin: 0 auto;
  padding: 0 15px;
}

.section-title {
  font-family: Gilroy-SemiBold, sans-serif;
  font-size: 54px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.header {
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  background-color: var(--background-color);
  border-bottom: 1px solid var(--border-color);
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 80px;
}
.header__logo img {
  display: block;
  max-height: 24px;
}
.header__menu-button {
  display: block;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  font-family: Gilroy-SemiBold, sans-serif;
  font-size: 16px;
  color: var(--text-color);
  text-transform: uppercase;
  letter-spacing: 1.5px;
}
.header__nav {
  display: none;
}
@media (min-width: 768px) {
  .header__menu-button {
    display: none;
  }
  .header__nav {
    display: block;
  }
  .header__nav-list {
    display: flex;
    align-items: center;
    gap: 35px;
    list-style: none;
    margin: 0;
    padding: 0;
  }
  .header__nav-link {
    text-decoration: none;
    font-size: 16px;
    font-family: Gilroy-Regular, sans-serif;
    color: var(--text-color);
    transition: color 0.3s ease;
  }
}

.promo__heading {
  min-height: calc(100vh - 80px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.promo__title {
  margin: 0;
  font-family: Gilroy-SemiBold, sans-serif;
  font-size: 44px;
  line-height: 1.1;
  text-transform: uppercase;
  letter-spacing: 2px;
}
.promo__subtitle {
  margin: 10px 0 0 0;
  font-family: Gilroy-Light, sans-serif;
  font-size: 22px;
}
.promo__scroll-down {
  margin-top: 30px;
}
.promo__about {
  padding-top: 50px;
}
.promo__photo {
  width: 100%;
  height: auto;
}
.promo__description {
  padding: 40px 0;
  font-size: 18px;
  text-align: center;
  line-height: 1.6;
  max-width: 700px;
  margin: 0 auto;
}
.promo__description strong {
  font-family: Gilroy-Bold, sans-serif;
}
.promo__about-title {
  margin: 0;
  padding: 40px 15px 20px;
  background-color: #5a5858;
  color: var(--background-color);
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 14px;
  font-family: Gilroy-Regular, sans-serif;
}
.promo__social-list {
  display: flex;
  justify-content: center;
  gap: 20px;
  list-style: none;
  margin: 0;
  padding: 0 15px 40px;
  background-color: #5a5858;
}
.promo__clients-logos {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px 40px;
  padding: 50px 0;
}

@media (min-width: 768px) {
  .promo__title {
    font-size: 64px;
  }
  .promo__subtitle {
    font-size: 30px;
  }
  .promo__description {
    font-size: 22px;
  }
}
@media (min-width: 1200px) {
  .promo__title {
    font-size: 86px;
  }
  .promo__subtitle {
    font-size: 36px;
  }
}
.what-i-do {
  padding: 60px 0;
}
.what-i-do__image-wrapper {
  background-image: url("../images/what-i-do-bg.jpg");
  background-size: cover;
  background-position: center;
  min-height: 250px;
  margin-bottom: 40px;
}
.what-i-do__title {
  text-align: left;
  margin-bottom: 40px;
}
.what-i-do__list {
  list-style: none;
  padding: 0;
  margin: 0;
  counter-reset: service-counter;
}
.what-i-do__item {
  padding: 30px 0;
  counter-increment: service-counter;
  border-top: 1px solid var(--border-color);
  display: flex;
  align-items: flex-start;
  gap: 25px;
}
.what-i-do__item:first-child {
  border-top: none;
  padding-top: 0;
}
.what-i-do__item:last-child {
  padding-bottom: 0;
}
.what-i-do__item::before {
  content: "0" counter(service-counter);
  font-family: Gilroy-Bold, sans-serif;
  color: var(--text-color);
  font-size: 18px;
  min-width: 30px;
}
.what-i-do__item-title {
  margin: 0 0 10px 0;
  font-family: Gilroy-Bold, sans-serif;
  text-transform: uppercase;
  font-size: 18px;
}
.what-i-do__item-text {
  margin: 0;
  font-family: Gilroy-Light, sans-serif;
  font-size: 16px;
  line-height: 1.6;
}
@media (min-width: 1200px) {
  .what-i-do__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 60px;
    align-items: center;
  }
  .what-i-do__image-wrapper {
    min-height: 600px;
    margin-bottom: 0;
  }
}

.button {
  display: inline-block;
  padding: 10px 24px;
  border-radius: 100px;
  text-decoration: none;
  font-family: Gilroy-Regular, sans-serif;
  font-size: 14px;
  border: 1px solid transparent;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}
.button--dark {
  background-color: var(--text-color);
  color: var(--button-text-color);
}
.button--light {
  background-color: transparent;
  color: var(--text-color);
  border-color: var(--border-color);
}
.button--light:hover {
  background-color: var(--text-color);
  color: var(--button-text-color);
}

.selected-work {
  padding: 60px 0;
}
.selected-work__title {
  margin-bottom: 50px;
  text-align: center;
}
.selected-work__gallery {
  display: flex;
  flex-direction: column;
  gap: 70px;
}
.selected-work__images-wrapper {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.selected-work__info {
  margin-top: 25px;
}
.selected-work .selected-work__project:nth-child(2) .selected-work__info {
  order: 2;
}
.selected-work .selected-work__project:nth-child(2) .selected-work__images-wrapper {
  order: 1;
}
.selected-work__project-title {
  margin: 0 0 20px 0;
  font-family: Gilroy-Bold, sans-serif;
  font-size: 24px;
  text-transform: uppercase;
}
.selected-work__tags {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 25px;
}
.selected-work__tag {
  display: inline-block;
  padding: 8px 20px;
  border: 1px solid var(--border-color);
  border-radius: 100px;
  text-decoration: none;
  color: var(--text-color);
  font-size: 14px;
  transition: all 0.3s ease;
}
.selected-work__tag:hover {
  background-color: var(--text-color);
  color: var(--button-text-color);
}
.selected-work__view-all-button {
  display: block;
  width: fit-content;
  margin: 70px auto 0;
}
@media (min-width: 1200px) {
  .selected-work__title {
    text-align: left;
  }
  .selected-work__project {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 100px;
    align-items: center;
  }
  .selected-work__info {
    margin-top: 0;
  }
  .selected-work__project:nth-child(even) .selected-work__info {
    text-align: right;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
}

.my-clients {
  padding: 60px 0;
}

.my-clients__title {
  margin-bottom: 50px;
  text-align: center;
}

.my-clients__list {
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.my-clients__testimonial {
  border-top: 1px solid var(--border-color);
  padding-top: 40px;
}
.my-clients__testimonial:first-child {
  border-top: none;
  padding-top: 0;
}

.my-clients__company-logo {
  margin-bottom: 25px;
  max-height: 24px;
  width: auto;
}

.my-clients__quote-title {
  margin: 0 0 15px 0;
  font-family: Gilroy-Bold, sans-serif;
  font-size: 22px;
  line-height: 1.4;
  color: var(--text-color);
}

.my-clients__quote-text {
  margin: 0 0 30px 0;
  font-family: Gilroy-Light, sans-serif;
  font-size: 16px;
  line-height: 1.6;
  color: var(--subtle-text-color);
}

.my-clients__author {
  display: flex;
  align-items: center;
  gap: 15px;
}

.my-clients__author-avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
}

.my-clients__author-name {
  margin: 0;
  font-family: Gilroy-Medium, sans-serif;
  font-size: 16px;
  color: var(--text-color);
}

.my-clients__author-role {
  margin: 4px 0 0 0;
  font-family: Gilroy-Light, sans-serif;
  font-size: 14px;
  color: var(--subtle-text-color);
}

@media (min-width: 768px) {
  .my-clients__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0;
  }
  .my-clients__testimonial {
    padding: 30px;
    border-top: none;
  }
  .my-clients__testimonial:nth-child(odd) {
    border-right: 1px solid var(--border-color);
  }
  .my-clients__testimonial:not(:nth-last-child(-n+2)) {
    border-bottom: 1px solid var(--border-color);
  }
}
@media (min-width: 1200px) {
  .my-clients__list {
    grid-template-columns: repeat(3, 1fr);
  }
  .my-clients__testimonial {
    padding: 40px;
    border-right: none;
    border-bottom: none;
  }
  .my-clients__testimonial:not(:nth-child(3n)) {
    border-right: 1px solid var(--border-color);
  }
  .my-clients__testimonial:nth-child(-n+3) {
    border-bottom: 1px solid var(--border-color);
  }
}
.our-team {
  padding: 60px 0;
}

.our-team__title {
  text-align: center;
  margin-bottom: 50px;
}

.our-team__list {
  display: flex;
  flex-direction: column;
  gap: 50px;
}

.our-team__member {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.our-team__member-photo-link {
  display: block;
  width: 100%;
  margin-bottom: 20px;
}

.our-team__member-photo {
  width: 100%;
}

.our-team__member-info-link {
  text-decoration: none;
  color: var(--text-color);
  margin-bottom: 20px;
}

.our-team__member-name {
  margin: 0 0 8px 0;
  font-family: Gilroy-Bold, sans-serif;
  font-size: 18px;
  text-transform: uppercase;
}

.our-team__member-role {
  margin: 0;
  font-family: Gilroy-Light, sans-serif;
  font-size: 14px;
  color: var(--subtle-text-color);
}

.our-team__social-list {
  display: flex;
  gap: 15px;
  padding: 0;
  margin: 0;
  list-style: none;
}

.our-team__cta {
  text-align: center;
}

.our-team__cta-subtitle {
  margin: 0 0 10px 0;
  font-family: Gilroy-Regular, sans-serif;
  font-size: 14px;
  text-transform: uppercase;
}

.our-team__cta-title {
  margin: 0 0 25px 0;
  font-family: Gilroy-SemiBold, sans-serif;
  font-size: 28px;
  line-height: 1.3;
}

@media (min-width: 768px) {
  .our-team__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 60px 30px;
  }
  .our-team__member {
    align-items: flex-start;
    text-align: left;
  }
  .our-team__member-photo-link {
    order: 2;
    margin-bottom: 0;
  }
  .our-team__member-info-link {
    order: 1;
    margin-bottom: 20px;
  }
  .our-team__social-list {
    order: 3;
  }
}
@media (min-width: 1200px) {
  .our-team__title {
    text-align: left;
  }
  .our-team__list {
    grid-template-columns: repeat(4, 1fr);
    align-items: start;
  }
  .our-team__member-photo {
    width: 315px;
    height: 325px;
  }
  .our-team__cta {
    grid-column: span 2;
    align-self: center;
    text-align: left;
    padding-left: 20px;
  }
}
.contact {
  padding: 100px 0;
  border-top: 1px solid var(--border-color);
  border-bottom: 1px solid var(--border-color);
}

.contact__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.contact__title {
  margin: 0 0 30px 0;
  font-size: 48px;
}

.contact__main-heading {
  margin: 0 0 25px 0;
  font-family: Gilroy-Bold, sans-serif;
  font-size: 28px;
  line-height: 1.3;
  max-width: 600px;
}

.contact__text {
  margin: 0 0 35px 0;
  font-family: Gilroy-Light, sans-serif;
  font-size: 16px;
  line-height: 1.6;
  max-width: 450px;
  color: var(--subtle-text-color);
}

@media (min-width: 1200px) {
  .contact__container {
    display: grid;
    grid-template-columns: 0.7fr 1fr;
    gap: 60px;
    align-items: center;
    text-align: left;
  }
  .contact__title {
    margin: 0;
    font-size: 120px;
    font-family: Gilroy-Regular, sans-serif;
    text-transform: uppercase;
    letter-spacing: 2px;
    justify-self: end;
  }
  .contact__main-heading {
    font-size: 40px;
    max-width: none;
  }
  .contact__text {
    font-size: 18px;
    max-width: none;
  }
}
.footer {
  padding: 40px 0;
  border-top: 1px solid var(--border-color);
}

.footer__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.footer__copyright {
  margin: 0;
  font-family: Gilroy-Regular, sans-serif;
  font-size: 14px;
  text-align: center;
  color: var(--subtle-text-color);
}

.footer__social-list {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 0;
  margin: 0;
  list-style: none;
}
.footer__social-list .social-list__link {
  transition: opacity 0.3s ease;
}
.footer__social-list .social-list__link:hover {
  opacity: 0.7;
}

@media (min-width: 768px) {
  .footer__container {
    flex-direction: row;
    justify-content: space-between;
  }
}