// Файл: src/scss/blocks/_header.scss

.header {
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;

  // ВИКОРИСТОВУЄМО CSS-ЗМІННІ
  background-color: var(--background-color);
  border-bottom: 1px solid var(--border-color);

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 80px;
  }

  &__logo {
    img {
      display: block;
      max-height: 24px;
    }
  }

  &__menu-button {
    display: block;
    padding: 0;
    border: none;
    background-color: transparent;
    cursor: pointer;
    font-family: $header-font, sans-serif;
    font-size: 16px;
    // ВИКОРИСТОВУЄМО CSS-ЗМІННУ
    color: var(--text-color);
    text-transform: uppercase;
    letter-spacing: 1.5px;
  }

  &__nav {
    display: none;
  }

  @include tablet {
    &__menu-button {
      display: none;
    }
    &__nav {
      display: block;
    }
    &__nav-list {
      display: flex;
      align-items: center;
      gap: 35px;
      list-style: none;
      margin: 0;
      padding: 0;
    }
    &__nav-link {
      text-decoration: none;
      font-size: 16px;
      font-family: $footer-font, sans-serif;
      // ВИКОРИСТОВУЄМО CSS-ЗМІННУ
      color: var(--text-color);
      transition: color 0.3s ease;
    }
  }
}
