// Файл: src/scss/blocks/_what-i-do.scss

.what-i-do {
  padding: 60px 0;

  &__image-wrapper {
    background-image: url("../images/what-i-do-bg.jpg");
    background-size: cover;
    background-position: center;
    min-height: 250px;
    margin-bottom: 40px;
  }

  &__title {
    text-align: left;
    margin-bottom: 40px;
  }

  &__list {
    list-style: none;
    padding: 0;
    margin: 0;
    counter-reset: service-counter;
  }

  &__item {
    padding: 30px 0;
    counter-increment: service-counter;
    // ВИКОРИСТОВУЄМО CSS-ЗМІННУ
    border-top: 1px solid var(--border-color);
    display: flex;
    align-items: flex-start;
    gap: 25px;
    &:first-child {
      border-top: none;
      padding-top: 0;
    }
    &:last-child {
      padding-bottom: 0;
    }
  }

  &__item::before {
    content: "0" counter(service-counter);
    font-family: $bold-font, sans-serif;
    // ВИКОРИСТОВУЄМО CSS-ЗМІННУ
    color: var(--text-color);
    font-size: 18px;
    min-width: 30px;
  }

  &__item-title {
    margin: 0 0 10px 0;
    font-family: $bold-font, sans-serif;
    text-transform: uppercase;
    font-size: 18px;
  }

  &__item-text {
    margin: 0;
    font-family: $light-font, sans-serif;
    font-size: 16px;
    line-height: 1.6;
  }

  @include desktop {
    &__container {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 60px;
      align-items: center;
    }
    &__image-wrapper {
      min-height: 600px;
      margin-bottom: 0;
    }
  }
}
